<template>
  <div class="infoQuery">
    <div class="infoQuery__query">
      <el-form :inline="true" size="small" :model="queryInfo" ref="queryForm">
        <el-form-item label="姓名">
          <el-input clearable v-model="queryInfo.name" placeholder="姓名"></el-input>
        </el-form-item>
        <el-form-item prop="num" label="证件号码">
          <el-input  clearable v-model="queryInfo.num" placeholder="证件号码" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="户籍地址">
          <el-input clearable v-model="queryInfo.household" placeholder="户籍地址"></el-input>
        </el-form-item>
        <el-form-item label="居住地址">
          <el-input  clearable v-model="queryInfo.live" placeholder="居住地址"></el-input>
        </el-form-item>
        <el-form-item label="人员情况">
          <el-select v-model="queryInfo.specialMarks" size="small"  multiple collapse-tags placeholder="请选择" clearable>
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onQuery(1)">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="populationQuery__table" v-loading="isLoading">
      <el-table style="width: 100%" border :data="tableData">
        <el-table-column label="序号" type="index" :index="indexMethod" width="96" align="center"></el-table-column>
        <el-table-column label="姓名" width="120" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="gotoMassesInfo(scope.row.id)">{{ scope.row.residentName }}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="residentIdType" label="证件类型" width="120" align="center"></el-table-column>
        <el-table-column prop="residentId" label="证件号码" width="180" align="center"></el-table-column>
        <el-table-column prop="residentSex" label="性别" width="60" align="center"></el-table-column>
        <el-table-column prop="residentType" label="人员类型" width="120" align="center"></el-table-column>
        <el-table-column prop="specialMark" label="人员情况" width="180" align="center"></el-table-column>
        <el-table-column prop="censusRegister" label="户籍地址" align="center"></el-table-column>
        <el-table-column prop="contactInfo" label="联系方式" width="120" align="center"></el-table-column>
        <el-table-column label="居住地址" align="center">
          <template slot-scope="scope">
            <el-button v-if="scope.row.addressMatchStatus !== 0" type="text" @click="gotoRoomInfo(scope.row.proCollId)">
              {{ scope.row.habitantAddress }}
            </el-button>
            <span v-else>--</span>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="text-align: right; padding: 20px 0;"
        background
        layout="total, prev, pager, next"
        :page-size="pageSize"
        :current-page="currentPage"
        :total="totalCount"
        @current-change="onCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import server from '../server.js'

export default {
  data () {
    return {
      queryInfo: {
        name: '',
        num: '',
        household: '',
        live: '',
        specialMarks:[]
      },
      options: [
          {
              value: '无案底人员',
              label: '无案底人员'
          },
          {
              value: '重点人员',
              label: '重点人员'
          }, {
              value: '风险人员',
              label: '风险人员'
          }, {
              value: '涉案人员',
              label: '涉案人员'
          }
      ],
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      totalCount: 0,
      isLoading: false
    }
  },
  mounted () {
      this.onQuery()
  },
  created () {
      console.log("返回条件：",this.$route.query)
      if(this.$route.query!=null){
          if(this.$route.query.old!=null){
              console.log("返回条件：",this.$route.query.old)
              this.queryInfo=this.$route.query.old.query
              this.currentPage=this.$route.query.old.currentPage
              this.totalCount=this.$route.query.old.totalCount
          }

      }
  },
  methods: {
    onQuery(currentPage = 1) {
      this.$refs.queryForm.validate((valid) => {
        if (valid) {
          this.currentPage = currentPage
          this.getServer()
        } else {
          return false;
        }
      })
    },
    getServer () {
        console.log(this.queryInfo.specialMarks)
        let  specialMark=""
        if(this.queryInfo.specialMarks.length>0){
           specialMark=this.queryInfo.specialMarks.join()
            this.pageSize=10000;
        }else{
            this.pageSize=10;
        }
      this.isLoading = true
      server.getPeopleInfo({
        residentName: this.queryInfo.name,
        residentId: this.queryInfo.num,
        censusRegister: this.queryInfo.household,
        habitantAddress: this.queryInfo.live,
        specialMark: specialMark,
        currentPage: this.currentPage,
        offset: 10
      }).then(res => {
        this.tableData = res.data
        this.totalCount = res.totalSize
      })
      this.isLoading = false
    },
    gotoMassesInfo (id) {
      this.$router.push({
        path: '/masses-info',
        query: { id,
            old:{
                query: this.queryInfo,
                currentPage: this.currentPage,
                totalCount: this.totalCount,
            }
        }
      })
    },
    gotoRoomInfo (id) {
      this.$router.push({
        path: '/room-info',
        query: {
          proCollId: id
        }
      })
    },
    onCurrentChange (val) {
      this.onQuery(val)
    },
    validateNumber (rule, value, callback) {
      if (typeof value === 'number' || value === '') {
        callback()
      } else {
        callback(new Error('请输入数字'))
      }
    },
    indexMethod(index) {
      return (index + 1) + (this.currentPage - 1) * 10
    }
  }
}
</script>

